@font-face {
  font-family: 'fontello';
  src: url('../font/fontello.eot?27471491');
  src: url('../font/fontello.eot?27471491#iefix') format('embedded-opentype'),
       url('../font/fontello.woff2?27471491') format('woff2'),
       url('../font/fontello.woff?27471491') format('woff'),
       url('../font/fontello.ttf?27471491') format('truetype'),
       url('../font/fontello.svg?27471491#fontello') format('svg');
  font-weight: normal;
  font-style: normal;
}
